
import moment from 'moment'
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import Card from 'vux/src/components/card/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Group from 'vux/src/components/group/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Divider from 'vux/src/components/divider/index.vue'


let echarts = require('echarts/lib/echarts')

require('echarts/lib/chart/bar')
require('echarts/lib/chart/line')

require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')
require('echarts/lib/component/markPoint')
export default{
mixins: [common],
components:{
Cell,
Card,
Group,
Flexbox,
Divider,
PopupPicker,
FlexboxItem
},
data () {
return {
active_cid: [],
active_student: {},
class_name: {},
classes: [],
students: [],
popup_chart: false,
charts: {},
yAxis: [],
series:[],
timeType: 'month'
}
},
watch: {
active_cid: function (val) {
if(val.length > 0){
let cid = parseInt(val[0])
this.get_students(cid)
this.active_student = {}
this.class_name = this.classes.find(c=>c.value===cid).name
}
},
timeType: function (val) {
this.changeStudent()
}
},
mounted() {
this.get_classes()
},
computed: {
enable_homework_star () {
let result = 0
if(this.$store.state.gvars.configs&&this.$store.state.gvars.configs.params){
result = this.$store.state.gvars.configs.params.service.enable_homework_star
}

return result
},
style () {
return {
opacity: this.popup_chart?'1':'0',
zIndex: this.popup_chart?'1':'-1',
position: this.popup_chart?'absolute':'relative',
with: '100%'
}
}
},
methods: {
setCurrentStudent (item) {
this.active_student = item
},
changeTime (type) {
this.timeType = type
},
getTimeDay () {
let time_type = this.timeType
if(time_type=='week') {
let week_day = moment(new Date()).weekday()
return moment(new Date()).subtract(week_day-1,'days').format('YYYYMMDD')
}else if(time_type=='month'){
return moment(new Date()).format('YYYYMM')+'01'
}else{
return moment(new Date()).subtract(2,'months').format('YYYYMM')+'01'
}
},
getData (type) {
let [list,data] = [this.charts.complete_list,[]]
if(list && list.length > 0){
list.forEach((l,index)=>{
if(type=='times'){
data.push('第'+(index+1)+'次')
}else{
data.push(l[type])
}
})
}
return data
},
changeStudent () {
let [sid,cid] = [this.active_student.sid,this.active_cid]
if(sid>0){
let start_day = this.getTimeDay()
let uri = util.sprintf('homework_completes/student_status?sid=%s&cid=%s&start_day=%s&end_day=%s',sid,cid,start_day,moment(new Date()).format('YYYYMMDD'))
this.$rest(uri).get()
.success(data=>{
this.charts = data
this.drawLine()
this.popup_chart = true

}).error(response=>{
this.toast(response.body.message,'text')
})
}else{
this.alert('提示','请先选择学员')
}
},
format_class (val) {
let tem_class = this.classes.find(c=>c.value==val)
if(tem_class){
return tem_class.name
}
return ''
},
get_classes () {
this.$rest('classes?teach_eid='+this.eid$).get()
.success(data=>{
let list = data.list
if(list.length > 0) {
this.classes = []
list.forEach(c=>{
this.classes.push({
value: c.cid,
name: c.class_name
})
})
}
}).error(response=>{
this.toast(response.body.message,'text')
})
},
get_students (cid) {
this.$http.get('classes/'+cid+'/students')
.then(response=>{
this.students = response.body.data.list
if(this.students.length === 0){
this.toast('该班级没有学员','text')
}
}).catch(response=>{
this.toast(response.body.message,'text')
})
},
drawLine() {
this.yAxis = []
if(this.enable_homework_star){
this.yAxis.push({
type : 'value', //value
min: 0,
max: 5,
interval: 1,
axisLine: {
lineStyle: {
width: '0'
}
},
axisLabel:{
textStyle: {
color: '#999999'
},
formatter: '{value} 星'
}
},{
type : 'value', //value
min: 0,
max: 10,
interval: 1,
axisLine: {
lineStyle: {
width: '0'
}
},
axisLabel:{
formatter: function (val) {
let ranges = {0:'',1:'D',2:'C-',3:'C',4:'C+',5:'B-',6:'B',7:'B+',8:'A-',9:'A',10:'A+'}
return ranges[val]
},
textStyle: {
color: '#999999'
}
}
})
this.series = [{
name:'星级',
type:'bar',
barWidth: '60%',
data: this.getData('star')
},
{
name:'评分',
type:'line',
yAxisIndex: 1,
smooth: true,
data: this.getData('result_level') ,
tooltip: {
trigger: 'item',
formatter: function (item) {
let ranges = {0:'',1:'D',2:'C-',3:'C',4:'C+',5:'B-',6:'B',7:'B+',8:'A-',9:'A',10:'A+'}
let dot = '<span style="display:inline-block;margin-right:5px;border-radius:10px;width:9px;height:9px;background-color:#3398DB;"></span>'
return  item.name+'<br>'+ dot + item.seriesName + '：' + ranges[item.value]
}
}
}]
}else{
this.yAxis.push({
type : 'value', //value
min: 0,
max: 4,
interval: 1,
axisLine: {
lineStyle: {
width: '0'
}
},
axisLabel:{
formatter: function (val) {
let ranges = {0:'0',1:'D',2:'C',3:'B',4:'A'}
return ranges[val]
},
textStyle: {
color: '#999999'
}
}
})
this.series = [{
name:'评分',
type:'bar',
barWidth: '60%',
data: this.getData('result_level') ,
tooltip: {
trigger: 'item',
formatter: function (item) {
let ranges = {0:'0',1:'D',2:'C',3:'B',4:'A'}
let dot = '<span style="display:inline-block;margin-right:5px;border-radius:10px;width:9px;height:9px;background-color:#3398DB;"></span>'
return  item.name+'<br>'+ dot + item.seriesName + '：' + ranges[item.value]
}
}
}]
}

let myChart = echarts.init(document.getElementById('myChart'))

myChart.setOption({
color: ['#3398DB','orange'],
tooltip : {
trigger: 'item',
axisPointer : {            // 坐标轴指示器，坐标轴触发有效
type : 'shadow'        
}
},
grid: {
left: '3%',
right: '4%',
bottom: '3%',
containLabel: true
},
xAxis : [
{
type : 'category',
data : this.getData('times'),//['第1次', '第2次', '第3次', '第4次', '第5次', '第6次', '第7次'],
axisTick: {
alignWithLabel: true
},
axisLabel:{
textStyle: {
color: '#999999'
}
}
}
],
yAxis : this.yAxis,
series : this.series
});
}
}
}




